      display: initial;
      padding: 0;
    }
  }

  .vertical-menu li .nav-link.active {
    color: $coloryellow;

    i {
      color: $coloryellow;
      opacity: 1;
    }
  }

  .footer {
    margin-top: auto;
    position: relative;
  }

  .copyright {
    color: #9c9ab3;
    font-size: 14px;
    transform: rotate(-180deg);
    writing-mode: vertical-rl;
    white-space: nowrap;
    display: inline-block;
    overflow: visible;
  }

  &.light {
    background: #f9f9ff;
    border-right: solid 1px rgba(0, 0, 0, 0.05);

    .vertical-menu {
      li {
        i {
          color: $colordark;
          opacity: 1;
        }
      }
    }
  }
}

header.mobile-header-1 {
  background: $colordark;
  display: none;
  padding: 10px 0;
  @include transition(0.3s);

  &.open {
    -webkit-transform: translateX(290px);
    -moz-transform: translateX(290px);
    -ms-transform: translateX(290px);
    -o-transform: translateX(290px);
    transform: translateX(290px);
  }

  .menu-icon {
    button {
      background: transparent;
      border: 0;
      cursor: pointer;
      outline: 0;
      height: 33px;
      width: 33px;
    }

    span {
      display: block;
      background: #fff;
      border-radius: 5px;
      height: 4px;
      position: relative;
      width: 30px;

      &:before {
        content: '';
        display: block;
        background: #fff;
        border-radius: 5px;
        height: 4px;
        top: -10px;
        position: absolute;
        width: 30px;
      }

      &:after {
        content: '';
        display: block;
        background: #fff;
        border-radius: 5px;
        height: 4px;
        top: 10px;
        position: absolute;
        width: 30px;
      }
    }
  }

  .site-logo {
    display: inline-block;

    img {
      max-height: 30px;
    }
  }

  &.light {
    background: #f9f9ff;

    .menu-icon span {
      background: $colordark;

      &:before,
      &:after {
        background: $colordark;
      }
    }
  }
}

